.container {
  display: block;
  max-width: 680px;
  width: 80%;
  margin: 120px auto;
}

.heading {
  color: #28a745;
  font-size: 48px;
  letter-spacing: -3px;
  text-align: center;
  margin: 120px 0 80px 0;
}

.links {
  list-style-type: none;
}

.links li {
  display: inline-block;
  margin: 0 20px 0 0;
  transition: 0.2s linear;
}

.links li:nth-child(2) {
  opacity: 0.6;
}

.links li:nth-child(2):hover {
  opacity: 1;
}

.links li:nth-child(3) {
  opacity: 0.6;
  float: right;
}

.links li:nth-child(3):hover {
  opacity: 1;
}

.links a {
  text-decoration: none;
  color: #0f132a;
  font-weight: bolder;
  text-align: center;
  cursor: pointer;
}

.input__block {
  margin: 20px auto;
  display: block;
  position: relative;
}

.input__block.first-input__block::before {
  content: "";
  position: absolute;
  top: -15px;
  left: 50px;
  display: block;
  width: 0;
  height: 0;
  background: transparent;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 15px solid rgba(15, 19, 42, 0.1);
  transition: 0.2s linear;
}

.input__block.signup-input__block::before {
  content: "";
  position: absolute;
  top: -15px;
  left: 150px;
  display: block;
  width: 0;
  height: 0;
  background: transparent;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 15px solid rgba(15, 19, 42, 0.1);
  transition: 0.2s linear;
}

.input {
  display: block;
  width: 90%;
  max-width: 680px;
  height: 50px;
  margin: 0 auto;
  border-radius: 8px;
  border: none;
  background: rgba(15, 19, 42, 0.1);
  color: rgba(15, 19, 42, 0.3);
  padding-left: 15px;
  font-size: 14px;
}

.input:focus,
.input:active {
  outline: none;
  border: none;
  color: rgba(15, 19, 42, 1);
}

.repeat__password {
  opacity: 0;
  display: none;
  transition: 0.2s linear;
}

.signin__btn {
  background: #218838;
  color: white;
  display: block;
  width: 92.5%;
  max-width: 680px;
  height: 50px;
  border-radius: 8px;
  margin: 20px auto 0;
  border: none;
  cursor: pointer;
  font-size: 14px;
  box-shadow: 0 15px 30px rgba(33, 136, 56, 0.36);
  transition: 0.2s linear;
}

.signin__btn:hover {
  box-shadow: 0 0 0 rgba(33, 136, 56, 0);
}

.separator {
  display: block;
  margin: 30px auto 10px;
  text-align: center;
  height: 40px;
  position: relative;
  background: transparent;
  color: rgba(15, 19, 42, 0.4);
  font-size: 13px;
  width: 90%;
  max-width: 680px;
}

.separator::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  background: rgba(15, 19, 42, 0.2);
  height: 1px;
  width: 45%;
}

.separator::after {
  content: "";
  position: absolute;
  top: 8px;
  right: 0;
  background: rgba(15, 19, 42, 0.2);
  height: 1px;
  width: 45%;
}

.google__btn,
.github__btn {
  display: block;
  width: 70%;
  max-width: 680px;
  margin: 20px auto;
  height: 50px;
  cursor: pointer;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  border-radius: 8px;
  border: none;
  line-height: 40px;
}

.google__btn {
  background: #5b90f0;
  color: white;
  box-shadow: 0 15px 30px rgba(91, 144, 240, 0.36);
  transition: 0.2s linear;
}

.google__btn .fa {
  font-size: 20px;
  padding: 0 5px 0 0;
}

.google__btn:hover {
  box-shadow: 0 0 0 rgba(91, 144, 240, 0);
}

.github__btn {
  background: #25282d;
  color: white;
  box-shadow: 0 15px 30px rgba(37, 40, 45, 0.36);
  transition: 0.2s linear;
}

.github__btn .fa {
  font-size: 20px;
  padding: 0 5px 0 0;
}

.github__btn:hover {
  box-shadow: 0 0 0 rgba(37, 40, 45, 0);
}
